<template>
  <div id="app">
    <div class="container-fluid">
        <Question v-if='state == "start"'
          :question='questions[stage].question'
          :answers='questions[stage].answers'
          :answerType='questions[stage].answerType'
          :stage='stage'
          :questionsLength='questions.length'
        @answer="getAnswer"
        ></Question>
        <FinalScreen v-else-if='state == "results"'
               :questions='questions'
               @again='restart'
        >
        </FinalScreen>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      questions: [
        {
          question: '    ?',
          answers: ['','',''],
          answerType: 'radio',
          correctAnswer: [''],
          userAnswer: [],
        },
        {
          question: '   ?',
          answers: ['a','span','div', 'select'],
          answerType: 'checkbox',
          correctAnswer: ['a','span'],
          userAnswer: [],
        },
        {
          question: '   " "?',
          answers: ['','','', ],
          answerType: 'radio',
          correctAnswer: [''],
          userAnswer: [],
        },
        {
          question: '    -?',
          answers: ['59 .','67 .','63 .', '69 .'],
          answerType: 'radio',
          correctAnswer: ['59 .'],
          userAnswer: [],
        },
        {
          question: '    ',
          answers: ['','','', '', '', ''],
          answerType: 'checkbox',
          correctAnswer: ['','','',''],
          userAnswer: [],
        },
        {
          question: '  ?',
          answers: [' '],
          answerType: 'text',
          correctAnswer: [''],
          userAnswer: [],
        },         

      ],
      stage: 0,
      state: 'start'
    }
  },

  methods: {
  	getAnswer(data) {
  		let object = this.questions[this.stage];
  		if ( Array.isArray(data.answer) ) {
  			object.userAnswer = data.answer
  		} else {
  			object.userAnswer.push(data.answer);
  		}
  		object.userAnswer.sort();
  		object.correctAnswer.sort();
  		if (this.stage == this.questions.length-1) {
  			this.state = 'results'
  		} else {
  			this.stage++; 
  		}
  	},
  	restart() {
  		this.stage = 0;
  		this.state = 'start'
  		this.questions.forEach((item) => {
  			item.userAnswer.length = 0;
  		})
  	}
  },
  computed: {

  },
}
</script>

<style>
#app {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



